import * as React from 'react';
import { ReactNode } from 'react';

export interface NewsFeedEntryDefinition {
    date: Date,
    title: string,
    body: ReactNode
}

export interface NewsFeedProps {
    id: string,
    entries: NewsFeedEntryDefinition[]
}

export function NewsFeed(props: NewsFeedProps) {
    const initialCount = 5;
    const stepCount = 5;

    const [ showCount, setShowCount ] = React.useState(initialCount);

    function incrementShowCount()
    {
        setShowCount(c => c + stepCount);
    }

    return <>
        <div className='card newsfeed-container'>
            <div className='card-body'></div>
            <div className='accordion' id={props.id}>
                {props.entries.slice(0, showCount).map((entry, idx) => {
                    const headerId = `newsItemHeader${idx}`;
                    const collapseId = `newsItemCollapse${idx}`;

                    return <>
                        <div className='accordion-item' key={idx}>
                            <h2 className='accordion-header' id={headerId}>
                                <button className='accordion-button collapsed' type='button' data-bs-toggle='collapse' data-bs-target={`#${collapseId}`} aria-expanded='false' aria-controls={collapseId}>
                                    <div className='row w-100'>
                                        <div className='col-sm-12 col-md-6 font-weight-bold date'>
                                            {entry.date.toLocaleDateString()}
                                        </div>
                                        <div className='col-sm-12 col-md-6 pt-2 pl-4'>
                                            {entry.title}
                                        </div>
                                    </div>
                                </button>
                            </h2>
                            <div className='accordion-collapse collapse m-3' id={collapseId} aria-labelledby={headerId} data-bs-parent={props.id}>
                                {entry.body}
                            </div>
                        </div>
                    </>
                })}
            </div>

            {showCount < props.entries.length ? <div className='w-100 p-2 text-center'>
                <button type='button' className='btn btn-link' onClick={incrementShowCount}>Show More</button>
            </div> : null}
        </div>
    </>
}