import * as React from 'react';
import { ReactNode } from 'react';

export interface SectionProps
{
    title?: string,
    children?: ReactNode
}

export function PageSection(props: SectionProps) {
    return <>
        <div className='row pt-5'>
            <h2 className='blood text-uppercase'>{props.title}</h2>
        </div>
        <div className='row'>
            {props.children}
        </div>
    </>
}