import { NewsFeed, NewsFeedEntryDefinition } from 'components/NewsFeed';
import { PageSection } from 'components/PageSection';
import { GetNewsFeed } from 'data/NewsFeedData';
import * as React from 'react';

export function MainPage()
{
    // Treat this like it's an API call for now, even though it's not
    const newsFeed = GetNewsFeed();
    newsFeed.sort((a, b) => (a.date.getTime() - b.date.getTime()));
    newsFeed.reverse();

    return <>
        <div className='container'>
            <PageSection title="Latest News">
                <NewsFeed id="newsFeed" entries={newsFeed} />
            </PageSection>
            <PageSection title="New Year New Us">
                <p>You can write news articles this way if you like. I've not quite decided which way I like it!</p>
            </PageSection>
        </div>
    </>
}